import React, { useEffect, useRef, useState } from "react";
import { useTranslation } from "react-i18next";
import Container from "react-bootstrap/Container";

// @scripts
import { convertStringToSlug } from "../../utility/utils";
import Avatar from "../../assets/avatar.jpg";

// @styles
import "../../styles/index.css";

function NavBar() {
  const [isButtonChecked, setIsButtonChecked] = useState(false);
  const [t] = useTranslation("global");
  const [isActiveNav, setIsActiveNav] = useState(t("nav.home").toLowerCase());
  const [showImage, setShowImage] = useState(false);
  const navRef = useRef();

  const changeImgBrand = () => {
    if (window.scrollY > 620) {
      setShowImage(true);
    } else {
      setShowImage(false);
    }
  }

  useEffect(() => {
    changeImgBrand()
    window.addEventListener("scroll", changeImgBrand)
  })

  return (
    <nav id="home" className="bg-white shadow-sm sticky-top" ref={navRef}>
      <Container className="d-flex justify-content-between align-items-center position-relative">
        <input
          type="checkbox"
          onClick={(e) => setIsButtonChecked(e.target.checked)}
          className="menu-btn"
          id="menu-btn"
        />
        <label className="menu-icon" htmlFor="menu-btn"><span className="navicon"></span></label>

        <ul className="d-flex flex-column flex-lg-row justify-content-lg-between list-unstyled fw-bold mb-0">
          <li className="nav-item">
            <a
              href={`${"#"}${convertStringToSlug(t("nav.home"))}`}
              className={`${t("nav.home").toLowerCase() === isActiveNav ? "active" : ""} nav-link d-block py-2`}
              onClick={() => setIsActiveNav(t("nav.home").toLowerCase())}
            >
              {t("nav.home")}
            </a>
          </li>
          <li className="nav-item">
            <a
              href={`${"#"}${convertStringToSlug(t("nav.about"))}`}
              className={`${isActiveNav === t("nav.about").toLowerCase() ? "active" : ""} nav-link d-block py-2`}
              onClick={() => setIsActiveNav(t("nav.about").toLowerCase())}
            >
              {t("nav.about")}
            </a>
          </li>
        </ul>
        {!isButtonChecked && (
          <ul className="d-flex flex-column flex-lg-row justify-content-lg-between list-unstyled fw-bold mb-0">
            {showImage ? (
              <li className="nav-brand text-center animate__animated animate__fadeInDown">
                <h6 className="mb-0 fw-light">Victor Zuluaga</h6>
                <p className="mb-0 text-muted">Software Developer</p>
              </li>
            ) : (
              <li className="nav-item-img animate__animated animate__fadeInUp">
                <img src={Avatar} alt="Victor Zuluaga avatar" width={170} height={170} className="img-avatar" />
              </li>
            )}

          </ul>
        )}
        <ul className="d-flex flex-column flex-lg-row justify-content-lg-between list-unstyled fw-bold mb-0">
          <li className="nav-item">
            <a 
              href={`${"#"}${convertStringToSlug(t("nav.skills"))}`}
              className={`${isActiveNav === t("nav.skills").toLowerCase() ? "active" : ""} nav-link d-block py-2`}
              onClick={() => setIsActiveNav(t("nav.skills").toLowerCase())}
            >
              {t("nav.skills")}
            </a>
          </li>
          <li className="nav-item">
            <a 
              href={`${"#"}${convertStringToSlug(t("nav.project"))}`}
              className={`${isActiveNav === t("nav.project").toLowerCase() ? "active" : ""} nav-link d-block py-2`}
              onClick={() => setIsActiveNav(t("nav.project").toLowerCase())}
            >
              {t("nav.project")}
            </a>
          </li>
          <li className="nav-item">
            <a 
              href={`${"#"}${convertStringToSlug(t("nav.blog"))}`}
              className={`${isActiveNav === t("nav.blog").toLowerCase() ? "active" : ""} nav-link d-block py-2`}
              onClick={() => setIsActiveNav(t("nav.blog").toLowerCase())}
            >
              {t("nav.blog")}
            </a>
          </li>
        </ul>
      </Container>
    </nav>
  );
}

NavBar.propTypes = {};

export default NavBar;
